/** @author 李文茜
 * @description 信用情况
*/
<template>
  <div>
      <div>
        <el-table :data="tableData" style="width: 100%; margin-top:24px">
          <el-table-column prop="business_name" label="公司名称" align="center"></el-table-column>
          <el-table-column prop="user_name" label="用户类型" align="center"></el-table-column>
          <el-table-column prop="credit_score" label="信用积分" align="center"></el-table-column>
          <el-table-column prop="credit_rating" label="信用等级" align="center"></el-table-column>
        </el-table>
      </div>
      <div style="display: block; text-align:left">
        <label style="margin-top:24px; display: inline-block; font-size:18px; font-weight:bold">信用记录</label><br>
        <label style="color:white; background:#75ace6; display: inline-block; margin-top:12px; padding:3px; margin-bottom:12px">数据统计：共{{total}}条</label>
        <el-table :data="recordData" style="width: 100%; display:inline-block">
          <el-table-column prop="user_behavior" align="center" label="用户行为"></el-table-column>
          <el-table-column prop="credit_type" align="center" label="奖惩类型"></el-table-column>
          <el-table-column prop="behavior_date" align="center" label="行为时间"></el-table-column>
          <el-table-column prop="integral_change" align="center" label="积分变动"></el-table-column>
          <el-table-column prop="integral_statistics" align="center" label="积分统计"></el-table-column>
        </el-table>
      </div>
      <div class="pagination_credit">
        <el-pagination background layout="prev, pager, next" :total="total" ></el-pagination>
      </div>
  </div>
</template>

<script>
export default {
  name: 'manageCredit',
  data () {
    return {
      tableData: [{
        business_name: '家政公司一',
        user_name: '家政公司',
        credit_score: 16800,
        credit_rating: '5级'
      }],
      recordData: [{
        user_behavior: '用户行为',
        credit_type: '奖惩类型',
        behavior_date: '行为时间',
        integral_change: '积分变动',
        integral_statistics: '积分统计'
      }],
      total: 1
    }
  },
  created () {
    // 面包屑
    this.$store.dispatch('commitChangeTopTitles', ['信用管理', '信用情况'])
  },
  mounted () {
  },
  methods: {
  }
}
</script>

<style scoped>
.credit_wrap{
  background: #000;
}
.el-table >>> th {
  background-color: #e3eefa;
  text-align: center;
}
.el-table >>> th  .cell {
  font-family: "苹方-简 常规体", "苹方-简 常规体", "苹方-简 常规体";
  color: #000000;
  font-size: 16px;
  font-weight: bold
}
.el-table >>> th:first-child .cell {
  font-family: "苹方-简 常规体", "苹方-简 常规体", "苹方-简 常规体";
  font-weight: bold;
  color: #000000;
}
.el-tabs--card >>> .el-tabs__header .el-tabs__item.is-active {
  border-bottom: #75ace6 5px solid;
  color: #000000;
  font-weight: bolder;
}
.pagination_credit {
  margin-top: 20px;
  text-align: center
}
</style>



